.header {
  background-color: var(--white);
  height: 100px;
  width: 100%;
  display: flex;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  z-index: 999;
  position: relative;
}

.header-logo {
  padding: 20px 10px 10px 30px;
}

.header-logo div {
  background-color: #020100;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.header-logo div::before {
  content: "Your Logo";
  color: #fdfffc;
  display: block;
  position: relative;
  top: 45%;
  font-weight: bold;
  text-align: center;
}

.header-nav .header-nav-item {
  display: inline-block;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}

.header-nav .header-nav-item ul {
  list-style: none;
  text-align: center;
}

.header-nav .header-nav-item ul li {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  padding: 0px 20px 0px 10px;
}

.header-nav .header-nav-item ul li a {
  text-decoration: none;
  color: #020100;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-left: 2px;
  font-size: 1.1em;
  transition: 200ms ease-in-out all;
}

.header-nav .header-nav-item ul li a:hover{
    color: var(--buttonsBlue);
}

.header-action {
  width: 55%;
  display: flex;
  justify-content: flex-end;
  padding: 10px;
}

.header-action .header-action-item {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}

.header-action-item-but1 {
    background-color: #235789 !important;
    color: var(--white) !important;
    padding: 6px 21px 6px 25px !important;
    border-radius: 1.1px !important;
}

.header-action-item-but2 {
    color: var(--buttonsBlue) !important;
    padding: 6px 20px 6px 20px !important;
    border-radius: 3px !important;
    border-color: var(--buttonsBlue) !important;
    border-width: 1.8px !important;
}