.branch-body {
    height: calc(100vh - 100px);
}

.branch-body::before {
    position: absolute;
    display: block;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/bg.jpg);
    background-position: center;
    background-size: cover;
    transform: scaleX(-1);
}

.branch-body-top {
    display: flex;
    flex-direction: row;
}

.branch-body-top-left {
    padding: 20px;
    position: relative;
}

.branch-body-top-left-title {
    font-family: 'Poppins', sans-serif;
    font-size: 64px;
    font-weight: 900;
    color: var(--buttonsBlue);
}

.branch-body-top-left-text {
    font-family: 'Poppins';
    font-size: 36px;
    color: var(--black);
    font-weight: 600;
}

.branch-body-top-left-btn1 {
    color: var(--white) !important;
    padding: 9px 35px 9px 35px !important;
    border-color: var(--white) !important;
    border-radius: 0px !important;
}

.branch-body-top-left-btn2 {
    background-color: var(--buttonsBlue) !important;
    color: var(--white) !important;
    padding: 9px 35px 9px 35px !important;
    border-radius: 0px !important;
}

.branch-body-top-right {
    position: relative;
    padding: 20px;
    margin: 40px 40px 0px 0px;
    width: 500px;
    transform-style: preserve-3d;
    overflow: hidden;
}

.branch-body-right-image {
    position: absolute;
    top: 30px;
    left: 0;
    width: 100%;
    height: 90%;
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transition: 300ms ease-in-out all;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transform: perspective(500px) rotateY(0deg);
}


.branch-body-right-image.branch-body-right-image-top{
    background-color: var(--yellow);
    background-image: url(../img/doing.png);
    background-position: center;
    background-size: 180%;
    background-repeat: no-repeat;
}

.branch-body-top-right:hover .branch-body-right-image.branch-body-right-image-top{
    transform: perspective(500px) rotateY(180deg);
}


.branch-body-right-image.branch-body-right-image-bottom{
    background-color: var(--buttonsBlue);
    background-image: url(../img/top2.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 180%;
    transform: perspective(500px) rotateY(180deg);
}

.branch-body-top-right:hover .branch-body-right-image.branch-body-right-image-bottom{
    transform: perspective(500px) rotateY(360deg);
}